
#source {
  text-align: center;
  margin-top: 5px;
}
a {
  text-align: center;
  color: white;
}
#app {
  min-width: 320px;
  max-width: 1080px; 
  background-color: #0b0c10;
  padding: 5px;
  margin-top: 50px;
}
.container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
#display {
  background-color: #1f2833;
}
.btn {
  text-align: center;
  width: 16%;
  height: 75px;
  margin-top: 10px;
  margin-left: 2%;
  margin-right: 2%;
  margin-bottom: 10px;
  font-size: 2em;
  background-color: #45a29e;
  border-color: #66fcf1;
  color: white;
}
.row {
  width: 100%;
}
h1 {
  font-weight: bold;
  text-align: center;
  font-size: 3em;
  color: #66fcf1;
}



